export let guiObj = {
  spotLightGui:{
    // x:0,
    "":'#fff',
    "":1,
  },
  ambientLightGui:{
    "":'#fff',
    "":1,
  },
  "":true,
  "Level":1
  // "":false
}
export let guiFun = gui => {
  gui.domElement.style="position:absolute;top:0px;left:0px"
  gui.domElement.onclick = function (e){
    e.stopPropagation()
  }
  gui.add(guiObj,"")
  let spotLightGui = gui.addFolder('')
// spotLightGui.add(guiObj.spotLightGui,'x',-1000,1000)
  spotLightGui.addColor(guiObj.spotLightGui,'')
  spotLightGui.add(guiObj.spotLightGui,'',0,4)

  let ambientLightGui = gui.addFolder('')
  ambientLightGui.addColor(guiObj.ambientLightGui,'')
  ambientLightGui.add(guiObj.ambientLightGui,'',0,2)
  gui.add(guiObj,'Level',{
    'Level 0': 0,
    'Level 1': 1,
    'Level 2': 2,
    'Level 3': 3,
    'Level 4': 4,
    'Level 5': 5
  })
  return gui
}
